@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&family=Poppins:wght@300;400;500;600&family=Roboto:wght@100;300;400;500;700;900&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Roboto', sans-serif;
}

::-webkit-scrollbar{
  display: none;
}

.container{
  max-width: 85%;
  margin: auto;
  @media screen and (max-width: 550px) {
    max-width: 95%;
  }
}
.flexSB{
  display: flex;
  justify-content: space-between;
}
.icon{
  border-radius: 50%;
  text-align: center;
  font-size: 2.6rem;
  padding: .2rem .7rem;
  background-color: rgba(255,255,255,0.3);
  color: #1eb2a6;
  transition: 0.5s;
  &:hover{
    cursor: pointer;
    background-color: #1eb2a6;
    color: #fff;
  }
}
button{
  border: none;
}
.row{
  width: 50%;
  @media screen and (max-width: 1200px) {
    width: 100%;
  }
}
button{
  padding: 17px 30px;
  background-color: #fff;
  color: #1eb2a6;
  font-weight: 600;
  border: none;
  margin: 30px 10px 0 0;
  border-radius: 3px;
  cursor: pointer;
  // box-shadow: 0 24px 36px -23px rgba(0, 0, 0, .2%);
  .right{
    margin-left: 20px;
  }
  &:hover{
    box-shadow: none;
  }
}
.primary-btn{
  background-color: #1eb2a6;
  color: #fff;
}

.grid{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  @media screen and (max-width: 968px) {
    grid-template-columns: repeat(2, 1fr);
  }
}
.grid2{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  @media screen and (max-width: 1299px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 968px) {
    grid-template-columns: repeat(1, 1fr);
    width: 60%;
  }
  @media screen and (max-width: 768px) {
    grid-template-columns: repeat(1, 1fr);
    width: 100%;
  }
}
.grid3{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 10px;
  @media screen and (max-width: 968px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 400px) {
    grid-template-columns: repeat(1, 1fr);
  }
}
.padding{
  padding: 80px 0;
}
.flex{
  display: flex;
  @media screen and (max-width: 400px) {
    flex-direction: column;
  }
}

.outline-btn{
  margin: 0;
  box-shadow: none;
  border: 2px solid #1eb2a6;
  width: 100%;
  transition: 0.5s;
  &:hover{
    background-color: #1eb2a6;
    color: #fff;
  }
}

.shadow{
  box-shadow: 0 5px 25px -2px rgba(0, 0, 0, .3%);
  background-color: #fff;
}






















/*=====================HEAD=====================*/



.head{
  padding: 20px 0;
  color: #fff;
  .logo{
    h1{
      font-size: 35px;
      font-weight: 900;
    }
  }
  .social{
    text-align: right;
    .icon{
      margin-left: 10px;
    }
    @media screen and (max-width: 470px) {
      margin: 0;
    }
  }
}



header{
  background-color: rgba(255,255,255,0.2);
  margin: 0 30px;
  position: relative;
  @media screen and (max-width: 1015px) {
    margin: 0;
    background-color: #1eb2a6;
    position: relative;
  }
  ul{
    padding: 30px 20px;
    li{
      margin-right: 40px;
      list-style-type: none;
      @media screen and (max-width: 1015px) {
        margin-bottom: 20px;
      }
      a{
        color: #fff;
        font-weight: 500;
        transition: 0.5s;
        text-decoration: none;
        &:hover{
          color: #1eb2a6;
        }
      }
    }
  }
  ul.flexSB{
    @media screen and (max-width: 1015px) {
      display: none;
    }
  }
  .mobile-nav{
    @media screen and (max-width: 1015px) {
      position: absolute;
      top: 7vh;
      left: 0;
      background-color: #1eb2a6;
      width: 100%;
    }
  }
  .start{
    background-color: #1eb2a6;
    padding: 30px 70px;
    color: #fff;
    clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
    @media screen and (max-width: 968px) {
      clip-path: none;
    }
    @media screen and (max-width: 550px) {
      padding: 20px 35px;
    }
  }
  .toggle{
    display: none;
    @media screen and (max-width: 1015px) {
      display: block;
      background: none;
      color: #fff;
      font-size: 30px;
      position: absolute;
      right: 30px;
      top: -20px;
      padding: 17px 30px;
    }
    @media screen and (max-width: 550px) {
      right: 0;
      padding: 5px 30px;
    }
  }
}



/*=====================HEAD=====================*/



.hero{
  background-image: url(./components/images/bg.webp);
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  padding-top: 20%;
  padding-right: 50px;
  color: #fff;
  @media screen and (max-width: 992px) {
    background-position: right;
    padding: 30% 0;
    height: 100vh;
    text-align: center;
  }
  @media screen and (max-width: 1024px) {
    padding-top: 33%;
  }
  @media screen and (max-width: 768px) {
    padding-top: 45%;
  }
  @media screen and (max-width: 530px) {
    padding-top: 60%;
  }
  @media screen and (max-width: 400px) {
    padding-top: 73%;
    background-position: center;
  }
  .row{
    h1{
      font-size: 45px;
      @media screen and (max-width: 480px) {
        font-size: 35px;
      }
    }
  }
}
.hero #heading h3{
  color: #fff;
}
.hero #heading{
  text-align: left;
  padding: 0;
  margin: 0;
}


p{
  line-height: 30px;
  font-size: 18px;
}

#heading{
  text-align: center;
  padding: 40px 0;
  h3{
    font-weight: 600;
    letter-spacing: 1px;
    color: #1eb2a6;
    text-transform: uppercase;
  }
  h1{
    font-size: 45px;
    margin: 20px 0;
    text-transform: capitalize;
    line-height: 50px;
    @media screen and (max-width: 400px) {
      font-size: 38px;   
     }
  }
}

.margin{
  // margin-top: 40%;
  @media screen and (max-width: 968px) {
    height: 20vh;
  }
}


/*=====================HEAD-CLOSE=====================*/


/*=====================ABOUT=====================*/


.back{
  background-image: url(./components/images/back.webp);
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-bottom: 30px;
  @media screen and (max-width: 968px) {
    background-position: center;
    padding-top: 15%;
    height: 95vh;
  }
  h1{
    font-size: 100px;
    font-weight: 400;
    @media screen and (max-width: 500px) {
      font-size: 80px;
    }
    @media screen and (max-width: 400px) {
      font-size: 60px;
    }
  }
  h2{
    font-weight: 500;
    font-size: 17px;
    text-transform: uppercase;
  }
}
.margin{
  margin-top: 40.3%;
}


.aboutHome{
  height: auto;
  padding: 15% 0;
  @media screen and (max-width: 1200px) {
    display: grid;
    grid-template-columns: 1fr;
  }
  .container{ 
  @media screen and (max-width: 1200px) {
    flex-direction: column-reverse;
  }
  }
  .left{
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .right{
    padding: 80px 50px;
    @media screen and (max-width: 650px) {
      padding: 110px 0;
    }
    @media screen and (max-width: 425px) {
      padding: 140px 0;
    }
  }
  .items{
    margin-top: 50px; 
  }
  .item{
    background-color: #fff;
    margin-top: 30px;
    padding: 20px;
    transition: 0.5s;
    @media screen and (max-width: 450px) {
      display: flex;
      flex-direction: column;
      text-align: center;
    }
    h2{
      font-size: 20px;
      margin-bottom: 15px;
    }
    &:hover{
      background-color: #1eb2a6;
      color: #fff;
      cursor: pointer;
      // box-shadow: 0 5px 25px -2px rgb(0 0 6%);
    }
  }
  .img{
    width: 200px;
    @media screen and (max-width: 650px) {
      margin: auto;
    }
  }
  img{
    width: 70px;
    height: 70px;
  }
}
.aboutHome #heading{
  text-align: left;
  padding: 0;
}

