/*=====================ABOUT-CLOSE=====================*/


/*=====================COURSES=====================*/

.coursesCard{
  padding: 80px 0;
  .items{
    background-color: #fff;
    padding: 30px;
    text-align: center;
    box-shadow: 0 5px 25px -2px rgba(0, 0, 0, .2%);
  }
  .img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #1eb2a6;
    padding: 15px;
    @media screen and (max-width: 400px) {
      margin: auto;
    }
  }
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .text{
    margin-left: 15px;
    text-align: left;
    h1{
      font-size: 25px;
      font-weight: 500;
      line-height: 40px;
      @media screen and (max-width: 430px) {
        font-size: 25px;
      }
      @media screen and (max-width: 400px) {
        text-align: center;
      }
      @media screen and (max-width: 386px) {
        font-size: 20px;
        line-height: 30px;
      }
    }
  }
  .rate{
    margin: 20px 0;
    color: #1eb2a6;
    .ic{
      font-size: 13px;
      margin-right: 5px;
    }
    @media screen and (max-width: 400px) {
     text-align: center;   
    }
  }
  .details{
    .box{
      display: flex;
      align-items: center;
      color: grey;
      margin-bottom: 30px;
    }
    span{
      color: #1eb2a6;
      font-weight: 500;
      font-size: 14px;
      margin-left: 30px;
      @media screen and (max-width: 400px) {
        margin-left: 45px;
       }
    }
    .dimg{
      width: 70px;
      height: 70px;
      margin-right: 20px;
      img{
        width: 100%;
        height: 70px;
        border-radius: 50%;
      }
    }
  }
  .price{
    margin: 30px 0;
    background-color: #f8f8f8;
    padding: 10px;
  }
  h3{
    font-weight: 500;
    color: #1eb2a6;
  }
}





.online{
  text-align: center;
  .box{
    box-shadow: 0 5px 25px -2px rgba(0, 0, 0, .1%);
    background-color: #fff;
    padding: 30px 20px;
    transition: 0.5s;
    &:hover{
      background-color: #1eb2a6;
      border-radius: 5px;
      cursor: pointer;
      h1{
        color: #fff;
      }
    }
    .img{
      width: 80px;
      height: 80px;
      margin: auto;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .show{
        opacity: 0;
      }
    }
  }
  h1{
    font-weight: 500;
    font-size: 20px;
    margin: 20px 0;
    line-height: 30px;
  }
  span{
    background-color: #f8f8f8;
    padding: 5px 20px;
    font-weight: 500;
    font-size: 15px;
    color: #1eb2a6;
    border-radius: 5px;
  }
}

.online .box:hover .show{
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
}

/*=====================COURSES-CLOSE=====================*/



/*=====================TEAM=====================*/


.team{
  .grid{
    @media screen and (max-width: 1300px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media screen and (max-width: 968px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 650px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .items{
    &:hover{
      background-color: #1eb2a6;
      color: #fff;
      cursor: pointer;
      p{
        color: #fff;
      }
      .overlay{
        opacity: 1;
      }
    }
  }
  .img{
    position: relative;
    height: 70%;
    @media screen and (max-width: 650px) {
      height: 80%;
    }
    @media screen and (max-width: 340px) {
      height: 74%;
    }
    img{
      width: 100%;
      height: 100%;
    }
  }
  .overlay{
    position: absolute;
    bottom: 0;
    z-index: 11;
    display: flex;
    flex-direction: column;
    padding: 20px;
    transition: 0.5s;
    opacity: 0;
    .icon{
      background-color: #000;
      color: #fff;
      margin: 5px;
    }
  }
  .details{
    padding: 30px;
    text-align: center;
    h2{
      font-size: 20px;
      font-weight: 500;
      transition: 0.5s;
    }
    p{
      font-size: 15px;
      color: grey;
      margin-top: 10px;
      transition: 0.5s;
    }
  }
}


/*=====================TEAM-CLOSE=====================*/

/*=====================TESTIMONIAL=====================*/


.testimonal{
  .items{
    padding: 30px;
    transition: 0.5s;
    &:hover{
      background-color: #1eb2a6;
      color: #fff;
      cursor: pointer;
      span{
        color: #fff;
      }
      p{
        color: #fff;
      }
      .icon{
        background-color: #fff;
        color: #1eb2a6;
      }
    }
    .img{
      position: relative;
      img{
        width: 100px;
        height: 100px;
        border-radius: 50%;
      }
      .icon{
        position: absolute;
        bottom: 0;
        left: 60px;
        background-color: #1eb2a6;
        color: #fff;
      }
    }
  }
  .name{
    margin: 25px;
    h2{
      font-size: 20px;
      margin-bottom: 5px;
    }
    span{
      color: #1eb2a6;
    }
  }
  p{
    margin-top: 20px;
    color: grey;
    transition: 0.5s;
  }
} 

/*=====================TESTIMONIAL-CLOSE=====================*/



/*=====================PRICING=====================*/


.price{
  @media screen and (max-width: 600px) {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .grid{
    @media screen and (max-width: 1200px) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
    @media screen and (max-width: 1015px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 600px) {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .items{
    padding: 40px;
    text-align: center;
    @media screen and (max-width: 1272px) {
      padding: 40px 20px;
    }
  }
  h1{
    font-size: 50px;
    margin: 20px 0;
    color: #1eb2a6;
    span{
      font-size: 20px;
      margin-right: 5px;
      color: #000;
    }
  }
  p{
    color: grey;
    margin: 40px 0;
  }
}





.faq{
  .container{
    max-width: 70%;
    border-radius: 5px;
    @media screen and (max-width: 700px) {
      max-width: 100%;
    }
  }
  .box{
    background-color: #fff;
    margin-bottom: 20px;
  }
  button{
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 15px 20px;
    @media screen and (max-width: 485px) {
      padding: 0;
    }
  }
  h2{
    font-weight: 500;
    @media screen and (max-width: 485px) {
      text-align: left;
    }
  }
  p{
    padding: 20px;
  }
  .accordion{
    transition: 0.5s;
    @media screen and (max-width: 485px) {
      display: flex;
      flex-direction: column;
    }
    &:hover{
      background-color: #1eb2a6;
      color: #fff;
    }
  }
}


/*=====================PRICING-CLOSE=====================*/


/*=====================BLOG=====================*/


.blog{
  img{
    width: 100%;
    height: 100%;
  }
  .text{
    padding: 40px 30px;
    .admin{
      @media screen and (max-width: 400px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        span{
          padding-bottom: 15px;
        }
      }
    }
  }
  h1{
    font-weight: 500;
    font-size: 22px;
    line-height: 30px;
    margin: 20px 0;
    transition: 0.5s;
    &:hover{
      color: #1eb2a6;
      cursor: pointer;
    }
  }
  span{
    text-transform: uppercase;
    font-weight: 500;
    color: #1eb2a6;
    &:nth-last-child(1){
      label{
        color: #1eb2a6;
      }
    }
    label{
      margin-left: 5px;
      color: grey;
      font-size: 14px;
    }
  }
  p{
    color: grey;
  }
}





/*=====================BLOG-CLOSE=====================*/


/*=====================CONTACT=====================*/

.contact{
  padding: 100px 0;
  .row{
    @media screen and (max-width: 1015px) {
      width: 100%;
    }
  }
  .container{
    @media screen and (max-width: 1015px) {
      flex-direction: column;
    }
  }
  iframe{
    width: 100%;
    height: 100%;
    border: none; 
    @media screen and (max-width: 1015px) {
      height: 50vh;
    }
  }
  .right{
    padding: 50px;
    @media screen and (max-width: 400px) {
      padding: 10px;
    }
  }
  h1{
    font-size: 30px;
    font-weight: 400;
  }
  p{
    margin: 30px 0;
  }
  .box{
    p{
      font-size: 17px;
      margin-top: 10px;
    }
  }
  textarea,input{
    width: 100%;
    padding: 15px;
    margin-bottom: 15px;
    outline: none;
    border: 1px solid grey;
  }
  .flexSB{
    input{
      &:nth-child(1){
        margin-right: 10px;
      }
    }
  }
  h3{
    margin-top: 20px;
    font-weight: 500;
    margin-bottom: 10px;
  }
  span{
    color: #1eb2a6;
    font-weight: 600;
    font-size: 15px;
    word-spacing: 5px;
  }
}



/*=====================CONTACT-CLOSE=====================*/

/*=====================FOOTER=====================*/

.newletter{
  background-color: #1eb2a6;
  margin-top: 50px;
  padding: 50px 0;
  color: #fff;
  .flexSB{
    @media screen and (max-width: 968px) {
      display: flex;
      flex-direction: column;
      text-align: center;
      padding: 0;
    }
  }
  .left{
    padding-right: 50px;
    @media screen and (max-width: 968px) {
      padding-bottom: 20px;
      padding-right: 0;
    }
  }
  .right{
    padding-right: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: 968px) {
      padding-right: 0;
    }
  }
  h1{
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 15px;
  }
  input{
    width: 100%;
    padding: 20px;
    border: none;
    outline: none;
    height: 30px;
    // margin-left: 30px;
  }
  .io{
    color: #fff;
    font-size: 24px;
    cursor: pointer;
  }
}




footer{
  background-color: #eeeeee;
  .container{
    display: grid;
    grid-template-columns: 3fr 2fr 2fr 4fr 3fr;
    grid-gap: 30px;
    @media screen and (max-width: 1015px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 460px) {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 5px;
    }
    .logo{
      span{
        color: #1eb2a6;
        font-size: 14px;
      }
      p{
        color: grey;
        margin: 30px 0 15px 0;
      }
      .icon{
        background-color: #1eb2a6;
        color: white;
        margin-right: 10px;
      }
    }
  }
  h3{
    margin-bottom: 40px;
    font-weight: 500;
  }
  .items{
    margin-bottom: 30px;
    img{
      width: 50px;
      height: 50px;
      border-radius: 5px;
      margin-right: 10px;
      object-fit: cover;
    }
    span{
      font-size: 12px;
      color: #1eb2a6;
      margin-right: 5px;
      text-transform: capitalize;
    }
    .icon{
      font-size: 12px;
      color: #1eb2a6;
      margin-right: 5px;
      text-transform: capitalize;
    }
  }
  .flexSB{
    @media screen and (max-width: 460px) {
      display: flex;
      flex-direction: column;
    }
  }
  ul{
    li{
      list-style-type: none;
      margin-bottom: 20px;
      &::before{
        content: "";
        color: #1eb2a6;
        margin-right: 5px;
      }
    }
  }
  h4{
    font-weight: 400;
    margin-top: 5px;
  }
  .last{
    ul{
      li{
        display: flex;
        margin-bottom: 50px;
      }
    }
    .icon{
      width: 60px;
      font-size: 20px;
      color: #1eb2a6;
    }
  }
}

.legal{
  text-align: center;
  width: 100%;
  padding: 50px 0;
  color: grey;
  background-color: #e9e9e9;
}


